import React, { useState, useEffect } from 'react';
import { useAuth } from '../../context/AuthContext';
import { useNavigate } from 'react-router-dom';

const Analytics = () => {
  const { user } = useAuth();
  const navigate = useNavigate();
  const [analytics, setAnalytics] = useState({
    totalRevenue: 0,
    totalOrders: 0,
    totalUsers: 0,
    averageOrderValue: 0,
    monthlyRevenue: [],
    topProducts: [],
    userGrowth: []
  });
  const [loading, setLoading] = useState(true);
  const [timeRange, setTimeRange] = useState('30d');

  useEffect(() => {
    if (!user || user.role !== 'Admin') {
      navigate('/login');
      return;
    }
    loadAnalytics();
  }, [user, navigate, timeRange]);

  const loadAnalytics = async () => {
    try {
      setLoading(true);
      // Mock data - replace with actual API calls
      const mockAnalytics = {
        totalRevenue: 125000,
        totalOrders: 450,
        totalUsers: 1200,
        averageOrderValue: 278,
        monthlyRevenue: [
          { month: 'Jan', revenue: 15000 },
          { month: 'Feb', revenue: 18000 },
          { month: 'Mar', revenue: 22000 },
          { month: 'Apr', revenue: 19000 },
          { month: 'May', revenue: 25000 },
          { month: 'Jun', revenue: 21000 }
        ],
        topProducts: [
          { name: 'Diamond Ring', sales: 45, revenue: 67500 },
          { name: 'Gold Necklace', sales: 32, revenue: 38400 },
          { name: 'Pearl Earrings', sales: 28, revenue: 22400 },
          { name: 'Silver Bracelet', sales: 25, revenue: 15000 }
        ],
        userGrowth: [
          { month: 'Jan', users: 100 },
          { month: 'Feb', users: 150 },
          { month: 'Mar', users: 200 },
          { month: 'Apr', users: 250 },
          { month: 'May', users: 300 },
          { month: 'Jun', users: 350 }
        ]
      };
      setAnalytics(mockAnalytics);
    } catch (error) {
      console.error('Failed to load analytics:', error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="analytics">
        <div className="loading-spinner">Loading analytics...</div>
      </div>
    );
  }

  return (
    <div className="analytics">
      <style>{`
        .analytics {
          min-height: 100vh;
          background: linear-gradient(135deg, #0f0f23 0%, #1a1a2e 50%, #16213e 100%);
          color: var(--text);
          padding: 2rem;
        }
        
        .page-header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-bottom: 2rem;
        }
        
        .page-title {
          font-size: 2rem;
          font-weight: 600;
          color: var(--accent);
        }
        
        .back-button {
          padding: 0.5rem 1rem;
          background: rgba(255,255,255,0.1);
          border: 1px solid rgba(255,255,255,0.2);
          border-radius: 6px;
          color: var(--text);
          cursor: pointer;
          transition: all 0.3s ease;
        }
        
        .back-button:hover {
          background: rgba(255,255,255,0.2);
        }
        
        .time-range-selector {
          display: flex;
          gap: 0.5rem;
          margin-bottom: 2rem;
        }
        
        .time-range-btn {
          padding: 0.5rem 1rem;
          background: rgba(255,255,255,0.05);
          border: 1px solid rgba(255,255,255,0.1);
          border-radius: 6px;
          color: var(--text);
          cursor: pointer;
          transition: all 0.3s ease;
        }
        
        .time-range-btn.active {
          background: var(--accent);
          color: white;
        }
        
        .stats-grid {
          display: grid;
          grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
          gap: 1.5rem;
          margin-bottom: 2rem;
        }
        
        .stat-card {
          background: rgba(255,255,255,0.05);
          border: 1px solid rgba(255,255,255,0.1);
          border-radius: 12px;
          padding: 1.5rem;
          backdrop-filter: blur(10px);
        }
        
        .stat-value {
          font-size: 2.5rem;
          font-weight: 700;
          color: var(--accent);
          margin-bottom: 0.5rem;
        }
        
        .stat-label {
          color: var(--muted);
          font-size: 0.9rem;
          margin-bottom: 0.5rem;
        }
        
        .stat-change {
          font-size: 0.8rem;
          font-weight: 500;
        }
        
        .stat-change.positive {
          color: #28a745;
        }
        
        .stat-change.negative {
          color: #dc3545;
        }
        
        .charts-grid {
          display: grid;
          grid-template-columns: 2fr 1fr;
          gap: 2rem;
          margin-bottom: 2rem;
        }
        
        .chart-card {
          background: rgba(255,255,255,0.05);
          border: 1px solid rgba(255,255,255,0.1);
          border-radius: 12px;
          padding: 1.5rem;
          backdrop-filter: blur(10px);
        }
        
        .chart-title {
          font-size: 1.2rem;
          font-weight: 600;
          color: var(--accent);
          margin-bottom: 1rem;
        }
        
        .chart-placeholder {
          height: 300px;
          background: rgba(255,255,255,0.03);
          border-radius: 8px;
          display: flex;
          align-items: center;
          justify-content: center;
          color: var(--muted);
          font-size: 1.1rem;
        }
        
        .revenue-chart {
          display: flex;
          align-items: end;
          height: 200px;
          gap: 0.5rem;
          margin-top: 1rem;
        }
        
        .revenue-bar {
          flex: 1;
          background: linear-gradient(to top, var(--accent), rgba(255,255,255,0.2));
          border-radius: 4px 4px 0 0;
          display: flex;
          align-items: end;
          justify-content: center;
          color: white;
          font-size: 0.8rem;
          font-weight: 500;
          padding: 0.5rem 0;
        }
        
        .products-list {
          display: flex;
          flex-direction: column;
          gap: 1rem;
        }
        
        .product-item {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 1rem;
          background: rgba(255,255,255,0.03);
          border-radius: 8px;
          border: 1px solid rgba(255,255,255,0.05);
        }
        
        .product-info {
          display: flex;
          flex-direction: column;
        }
        
        .product-name {
          font-weight: 500;
          color: var(--text);
          margin-bottom: 0.25rem;
        }
        
        .product-sales {
          font-size: 0.85rem;
          color: var(--muted);
        }
        
        .product-revenue {
          font-weight: 600;
          color: var(--accent);
        }
        
        .loading-spinner {
          display: flex;
          justify-content: center;
          align-items: center;
          height: 200px;
          font-size: 1.1rem;
          color: var(--muted);
        }
        
        @media (max-width: 768px) {
          .charts-grid {
            grid-template-columns: 1fr;
          }
        }
      `}</style>

      <div className="page-header">
        <h1 className="page-title">Analytics Dashboard</h1>
        <button className="back-button" onClick={() => navigate('/admin')}>
          ← Back to Dashboard
        </button>
      </div>

      <div className="time-range-selector">
        <button
          className={`time-range-btn ${timeRange === '7d' ? 'active' : ''}`}
          onClick={() => setTimeRange('7d')}
        >
          Last 7 Days
        </button>
        <button
          className={`time-range-btn ${timeRange === '30d' ? 'active' : ''}`}
          onClick={() => setTimeRange('30d')}
        >
          Last 30 Days
        </button>
        <button
          className={`time-range-btn ${timeRange === '90d' ? 'active' : ''}`}
          onClick={() => setTimeRange('90d')}
        >
          Last 90 Days
        </button>
      </div>

      <div className="stats-grid">
        <div className="stat-card">
          <div className="stat-value">${analytics.totalRevenue.toLocaleString()}</div>
          <div className="stat-label">Total Revenue</div>
          <div className="stat-change positive">+12.5% from last month</div>
        </div>
        <div className="stat-card">
          <div className="stat-value">{analytics.totalOrders}</div>
          <div className="stat-label">Total Orders</div>
          <div className="stat-change positive">+8.3% from last month</div>
        </div>
        <div className="stat-card">
          <div className="stat-value">{analytics.totalUsers}</div>
          <div className="stat-label">Total Users</div>
          <div className="stat-change positive">+15.2% from last month</div>
        </div>
        <div className="stat-card">
          <div className="stat-value">${analytics.averageOrderValue}</div>
          <div className="stat-label">Average Order Value</div>
          <div className="stat-change positive">+4.1% from last month</div>
        </div>
      </div>

      <div className="charts-grid">
        <div className="chart-card">
          <h3 className="chart-title">Revenue Trend</h3>
          <div className="revenue-chart">
            {analytics.monthlyRevenue.map((item, index) => (
              <div key={index} className="revenue-bar" style={{ height: `${(item.revenue / 25000) * 100}%` }}>
                ${item.revenue.toLocaleString()}
              </div>
            ))}
          </div>
        </div>

        <div className="chart-card">
          <h3 className="chart-title">Top Products</h3>
          <div className="products-list">
            {analytics.topProducts.map((product, index) => (
              <div key={index} className="product-item">
                <div className="product-info">
                  <div className="product-name">{product.name}</div>
                  <div className="product-sales">{product.sales} sales</div>
                </div>
                <div className="product-revenue">${product.revenue.toLocaleString()}</div>
              </div>
            ))}
          </div>
        </div>
      </div>

      <div className="chart-card">
        <h3 className="chart-title">User Growth</h3>
        <div className="chart-placeholder">
          📈 User growth chart would be displayed here
          <br />
          <small>Integration with charting library needed</small>
        </div>
      </div>
    </div>
  );
};

export default Analytics;
